<div class="center-form container"><div class="form-row"><div class="form-group col-md-6"><label for="documentTitle">Title of document</label><input class="form-control" id="documentTitle" placeholder="Enter Title" v-model="document.title"><small id="titleTag">A default roman numeral titled document will be generated otherwise</small><label for="documentDescription">Description of Document</label><input class="form-control" id="documentDescription" placeholder="Enter Description" v-model="document.description"><small class="mb-3" id="titleTag">A default description will be generated otherwise</small><select class="form-select form-control mb-3" :class="{ 'is-invalid': isCategoryInvalid }" id="category" aria-label="Category" style="width:auto" v-model="document.category" required><option disabled value="">--Select Category--</option><option v-for="category in categories" :key="category">{{ category }}</option></select></div></div><div><button class="btn btn-success" @click="submitForm">Submit</button></div></div>